.container.music{
  display: flex;
  flex-flow: column wrap;
  align-content: space-between;
  /* Masonry: Your container needs a fixed height, and it
   * needs to be taller than your tallest column. */
  height: 2500px;

  h2{
    font-size: 1.25rem;
  }
  article {
    --img-scale: 1.001;
    --title-color: black;
    --link-icon-translate: -20px;
    --link-icon-opacity: 0;
    position: relative;
    border-radius: 16px;
    box-shadow: none;
    background: #fff;
    filter: drop-shadow(1px 2px 8px #8080803d);
    transform-origin: center;
    transition: all 0.4s ease-in-out;
    overflow: hidden;
    margin: 8px;
  }

/* Masonry: Re-order items into 3 rows */
  article:nth-child(3n+1) { order: 1; }
  article:nth-child(3n+2) { order: 2; }
  article:nth-child(3n)   { order: 3; }


  /* Masonry: force new columns */
  &::before,
  &::after {
    content: "";
    flex-basis: 100%;
    width: 0;
    order: 2;
  }

  figure {
    margin: 0;
    padding: 0;
    aspect-ratio: 16 / 9;
    overflow: hidden;
  }

  article img {
    max-width: 100%;
    transform-origin: center;
    transform: scale(var(--img-scale));
    transition: transform 0.4s ease-in-out;
  }

  .article-body {
    padding: 24px;
  }

  article a {
    display: inline-flex;
    align-items: center;
    text-decoration: none;
    color: #28666e;
  }

  article a:focus {
    outline: 1px dotted #28666e;
  }

  article a .icon {
    min-width: 24px;
    width: 24px;
    height: 24px;
    margin-left: 5px;
    transform: translateX(var(--link-icon-translate));
    opacity: var(--link-icon-opacity);
    transition: all 0.3s;
  }

  /* using the has() relational pseudo selector to update our custom properties */
  article:has(:hover, :focus) {
    --img-scale: 1.1;
    --title-color: #28666e;
    --link-icon-translate: 0;
    --link-icon-opacity: 1;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  }
}


