}
.cd-timeline-content:after {
  content: "";
  display: table;
  clear: both;
}
.cd-timeline-content h2 {
  color: #3a3a3a;
  margin-top:0;
  margin-bottom: 5px;
}
.cd-timeline-content p,
.cd-timeline-content .cd-date,
.cd-timeline-content .cd-place{
  color: black;
  font-size: 13px;
  font-size: 0.8125rem;
}
.cd-timeline-content .cd-date,
.cd-timeline-content .cd-place{
  display: inline-block;
}
.cd-timeline-content p {
  margin: 0;
  line-height: 1.5;
}

.cd-timeline-content::before {
  content: '';
  position: absolute;
  top: 16px;
  right: 100%;
  height: 0;
  width: 0;
  border: 15px solid transparent;
  border-right: 15px solid #FFF;
}

@media only screen and (min-width: 768px) {
  .cd-timeline-content h2 {
    font-size: 20px;
    font-size: 1.25rem;
  }
  .cd-timeline-content p {
    font-size: 16px;
    font-size: 1rem;
  }
  .cd-timeline-content .cd-read-more,
  .cd-timeline-content .cd-date,
  .cd-timeline-content .cd-place {
    font-size: 14px;
    font-size: 0.875rem;
  }
}
@media only screen and (min-width: 1170px) {
  .cd-timeline-content {
    color: white;
    margin-left: 0;
    padding: 1.6em;
    width: 36%;
    margin: 0 5%;
    top: 10px;
    filter: drop-shadow( 1px 2px 8px #8080803d );
  }
  .cd-timeline-content::before {
    top: 14px;
    left: 100%;
    border-color: transparent;
    border-left-color: #FFF;
  }
  .cd-timeline-content .cd-place {
    position: absolute;
    width: 100%;
    left: 122%;
    top: 6px;
    font-size: 16px;
    font-size: 1rem;
    font-weight: bold;
  }
  .cd-timeline-content .cd-date {
    position: absolute;
    width: 100%;
    left: 122%;
    top: 30px;
    font-size: 16px;
    font-size: 1rem;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content {
    float: right;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content::before {
    top: 14px;
    left: auto;
    right: 100%;
    border-color: transparent;
    border-right-color: #FFF;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-read-more {
    float: right;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-place {
    left: auto;
    right: 122%;
    text-align: right;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-date {
    left: auto;
    right: 122%;
    text-align: right;
  }
}